@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:600&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: rgba(180, 180, 180, 0.2);
}

body{font-family:"Source Sans Pro", sans-serif}.app{width:80%;height:100%;min-height:100vh;display:flex;flex-direction:row;justify-content:center;align-items:center;margin:0 auto}.app__container{font-size:1rem;z-index:10;overflow:hidden;position:relative;width:100%;background-color:rgba(180,180,180,0.5);border-radius:13px;padding:0.8rem;display:flex;flex-direction:row;justify-content:flex-start;align-items:center;transition:all 0.5s ease-in-out}.app__container:hover{background-color:#b4b4b4}.app__container_focus{background-color:#f8f9fb}.app__container_focus:hover{background-color:#f8f9fb}.app__container_focus .custom-checkbox__input{width:1.4rem;transform:translateX(0)}.app__double-slash{display:inline-block;color:#dba606;animation-timing-function:linear;animation-name:double-slash;animation-duration:1s}.app__task-input,.app__note-input{min-height:20px;font-size:1rem;font-family:"Source Sans Pro", sans-serif;background-color:transparent;border:none;outline:none;flex:1 1}.app__note-input{padding-left:0.2rem;color:#dba606}.app__note-input::-webkit-input-placeholder{color:#dba606;opacity:0.3}.app__note-input:-ms-input-placeholder{color:#dba606;opacity:0.3}.app__note-input::placeholder{color:#dba606;opacity:0.3}.app__dropdown-container{display:block;position:relative;overflow:visible}.app__dropdown-titile,.app__calendar-logo{display:flex;flex-direction:row;justify-content:center;align-items:center;background-color:#f0f0f0;padding:0.4rem;border-radius:10px}.app__calendar-logo{margin-right:0.4rem}.app__calendar-logo svg{color:#686868}.app__dropdown-titile{color:#686868}.app__dropdown-titile>.app__title-circle-logo{font-size:0.7rem;color:#b4b4b4}.app__dropdown-titile>.app__title-arrow-logo{font-size:1.3rem}.app__title-circle-logo{margin-right:0.4rem;margin-left:0.2rem}.app__title-arrow-logo{margin-left:0.4rem}@keyframes double-slash{0%{transform:scale(1)}25%{transform:scale(1.13)}50%{transform:scale(1.25)}75%{transform:scale(1.13)}100%{transform:scale(1)}}

.custom-checkbox__input{z-index:-2;margin-right:0.5rem;position:relative;width:0;height:1.4rem;background-color:rgba(180,180,180,0.2);border-radius:7px;-webkit-appearance:none;-moz-appearance:none;appearance:none;outline:none;cursor:pointer;transform:translateX(-2rem);transition:all 0.5s ease-in-out}.custom-checkbox__input::before{position:absolute;content:"";display:block;top:2px;left:7px;width:6px;height:12px;border-style:solid;border-color:#f8f9fb;border-width:0 2px 2px 0;transform:rotate(45deg);opacity:0}.custom-checkbox__input:checked{background:#06842c}.custom-checkbox__input:checked::before{opacity:1}

